import { Component, OnInit, Input } from '@angular/core';
import template from './fox-resultados.component.html'
import style from './fox-resultados.component.css'
import axios from 'axios'

@Component({
  selector: 'app-fox-resultados',
  template: template + '',
  styles: [style + '']
})
export class FoxResultadosComponent implements OnInit {
  @Input() competitionid: string = '';
  @Input() lang: string = 'es';
  @Input() date: string = '' + (new Date()).getFullYear() + ((new Date()).getMonth() + 1) + (new Date()).getDate();
  @Input() max: number = 0;
  @Input() type: string = 'futbol';
  @Input() country: string = 'ar';

  @Input() oninit: Function;
  @Input() onerror: Function;
  @Input() onMatchClick: Function;
  @Input() onWatchLive: Function;
  @Input() onWatch: Function;

  title = 'Resultados';
  dates = [];

  constructor() {}

  ngOnInit() {
    // Usefull variables
    const _this = this;
    const params = {};
    const sortedRows = {};
    const monthLabels = {
      'es': ['Enero','Febrero','Marzo','Abril','Mayo','Junio', 'Julio','Augosto','Septiembre','Octubre','Noviembre','Diciembre'],
      'pt': ['Janeiro', 'Fevereiro', 'MarÃ§o', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro']
    };
    const today = parseInt('' + (new Date()).getFullYear() + ((new Date()).getMonth() + 1) + (new Date()).getDate())

    // API Params setup
    if (_this.country) params['country-code'] = _this.country;
    if (_this.type) params['type'] = _this.type;
    if (_this.date) params['dates-array'] = _this.date;
    if (_this.competitionid) params['competition-id'] = _this.competitionid;

    axios.get('https://stats.foxsportsla.com/stats/get_results_v2', {
      params: params
    }).then(function(resp) {

      // Sort results and group by date
      resp.data.forEach(row => {
        const key = parseInt(row.year + '' + row.month + '' + row.day);
        if (!sortedRows[key]) sortedRows[key] = []
        sortedRows[key].push(row);
      });

      // Prepare data for frontend
      Object.keys(sortedRows).forEach(date => {
        const newDate = {
          label: sortedRows[date][0].day + ' ' + monthLabels[_this.lang][(sortedRows[date][0].month - 1)] + ' ' + sortedRows[date][0].year,
          matches: []
        };
        sortedRows[date].forEach(match => {
          let status = '';
          if (match.status === 'final') {
            status = 'Final';
          } else {
            status = match['live-match']['live-minute'] + ' ';
            status = status + (match['live-match']['total-periods'] === 1 ? 'PT' : 'ST')
          }

          const homeTotalGoals = (match['home-score'] ? parseInt(match['home-score']) : 0) + (match['aggregate-home-team-goals'] ? parseInt(match['aggregate-home-team-goals']) : 0)
          const visitTotalGoals = (match['visiting-score'] ? parseInt(match['visiting-score']) : 0) + (match['aggregate-visiting-team-goals'] ? parseInt(match['aggregate-visiting-team-goals']) : 0)

          let newMatch = {
            rawData: match,
            home: {
              img: match['home-team']['team-logo-url'],
              name: match['home-team']['team-name'],
              goals: match['home-score'],
              extragoals: match['aggregate-home-team-goals'],
              winner: homeTotalGoals > visitTotalGoals
            },
            away: {
              img: match['visiting-team']['team-logo-url'],
              name: match['visiting-team']['team-name'],
              goals: match['visiting-score'],
              extragoals: match['aggregate-visiting-team-goals'],
              winner: homeTotalGoals < visitTotalGoals
            },
            hasEnded: match.status === 'final',
            isLive: match['live-match']['live-minute'] !== 0,
            status: status,
            link: 'https://www.google.com.ar',
            place: match.stadium
          };
          newDate.matches.push(newMatch);
        })

        _this.dates.push(newDate);
      });

      // Run parent init if available
      if (_this.oninit) _this.oninit(_this.dates);
    }).catch(err => {
      if (_this.onerror) _this.onerror(err);
    })
  }

}
